.pagination {
    display: inline-block;
    padding: 0;
    margin: 0;
}

.pagination a {
    display: inline;
}

.pagination a {
    color: black;
    float: left;
    padding: 10px 16px;
    text-align: center;
    text-decoration: snow;
    transition: background-color .3s;
    border-radius: 5px;
}

.pagination a.active {
    background-color: #4CAF50;
    color: white;

}

.pagination a.fake {

}

.pagination a:hover:not(.active,.fake) {
    background-color: rgba(82, 81, 81, 0.35);
}